import React, { useEffect, useState } from 'react'
import './style.css'
import { useColourObjectStore } from '../../../../store/ColourStore';
import { useLanguageStore } from '../../../../store/LanguageStore'


const Reward = (props: any) => {
  const { colourObj } = useColourObjectStore();
  const { completion_text, completion_image } = props;
  const { Language, setLanguage } = useLanguageStore();
  const [completion_text1, setcompletion_text1] = useState("Congratulations you've successfully completed this level!")

  useEffect(() => {
    if (completion_text === undefined) {
      setcompletion_text1(completion_text)
    }
  }, [])

  return (
    <div className="reward_container">
      
     {(Language?.value === "KANNADA") ?
        <>
        <div className='reward_text' style={{ color: colourObj.color_m1 }}>
        {(props.completed_game === true) ?
          "ಅಭಿನಂದನೆಗಳು! ನೀವು ಸಂಪೂರ್ಣ ಆಟವನ್ನು ಯಶಸ್ವಿಯಾಗಿ ಪೂರ್ಣಗೊಳಿಸಿದ್ದೀರಿ."
          :
          "ಅಭಿನಂದನೆಗಳು! ನೀವು ಈ ಹಂತವನ್ನು ಯಶಸ್ವಿಯಾಗಿ ಪೂರ್ಣಗೊಳಿಸಿದ್ದೀರಿ."
        }
        <br />
        <br />
        {(props.completed_game === true) &&
          "ನೀವು ಆಟವನ್ನು ಆನಂದಿಸಿದ್ದೀರಿ ಎಂದು ನಾವು ಭಾವಿಸುತ್ತೇವೆ!"
        }
        </div>
       <div className='reward_img_wrapper'>
        <img className='reward_img' src={completion_image} />
       </div>
        <div className='reward_text1' style={{ color: colourObj.color_m1 }}>
        ಹೋಮ್‌ಸ್ಕ್ರೀನ್‌ಗೆ ಹಿಂತಿರುಗಲು ಕೆಳಗಿನ ಟಿಕ್ ಮಾರ್ಕ್ ಅನ್ನು ಟ್ಯಾಪ್ ಮಾಡಿ.
        </div>
      </>
      :
      <>
      <div className='reward_text' style={{ color: colourObj.color_m1 }}>
      {(props.completed_game === true) ?
        "Congratulations you've successfully completed the entire game!"
        :
        completion_text1
      }
      <br />
      <br />
      {(props.completed_game === true) &&
        "We hope you enjoyed playing!"
      }
    </div>
    <div className='reward_img_wrapper'>
      <img className='reward_img' src={completion_image} />
    </div>
    <div className='reward_text1' style={{ color: colourObj.color_m1 }}>
      Tap the tick mark below to go back to the homescreen.
    </div>
   </>
  }
       
    </div>
  )
}

export default Reward
